  base64toBlob(base64, type) {
    // base64Unicode
    let bstr = atob(base64, type),
      n = bstr.length,
      u8arr = new Uint8Array(n);
    while (n--) {
      u8arr[n] = bstr.charCodeAt(n); // charCodeAt Unicode
    }
    return new Blob([u8arr], {
      type,
    });
  }

