/* global wps:false */

export function insert(param,row,col,text) {
    switch (param) {
        case "": {
            addPictrue()
            break
        }

        case "": {
            addTable(row,col)
            break
        }

        case "": {
            addString(text)
            break
        }
        default:
    }
}

function addString(text) {
    console.log("");
    wps.WpsApplication().Selection.InsertAfter(text);       //
}
function addTable(row,col) {
    console.log("");
    //
    // var endIndex=wps.Application.Selection.End;    //
    //  let myRange = wps.Application.ActiveDocument.Range(endIndex, endIndex)    //range
    //  wps.Application.ActiveDocument.Tables.Add(myRange, 3, 4,1,0)     //range
    //
    var tableNew = wps.Application.ActiveDocument.Tables.Add(wps.Application.Selection.Range, row, col, 1, 0);
    tableNew.AllowAutoFit = true;
}
